import React from 'react';
import { graphql } from 'gatsby';
import { Box } from '@material-ui/core';
import Bio from '../components/bio';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { TextLink, Body2, Body1, H6, Quote } from '../components/atoms';

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const posts = data.allMarkdownRemark.edges;

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="All posts" />
      <Bio />
      <Quote>
        <p>"The more you know, the more you know you don't know"</p>
        <br />
        <p className="author">- Aristotle</p>
      </Quote>
      {posts.map(({ node }) => {
        const title = node.frontmatter.title || node.fields.slug;
        return (
          <article key={node.fields.slug}>
            <Box my={3}>
              <header>
                <H6 my={1}>
                  <TextLink color="info.main" to={node.fields.slug}>
                    {title}
                  </TextLink>
                </H6>
                <Body2 mb={1} fontWeight="fontWeightLight">
                  {node.frontmatter.date}
                </Body2>
              </header>
              <section>
                <Body1
                  dangerouslySetInnerHTML={{
                    __html: node.frontmatter.description || node.excerpt,
                  }}
                />
              </section>
            </Box>
          </article>
        );
      })}
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { isActive: { eq: true } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`;
